/**=====================
    60. RTL CSS Start
==========================**/

.rtl {
  ul {
    padding-right: 0;
  }

  .ml-4 {
    margin-left: unset !important;
    margin-right: 1.5rem !important;
  }

  .pull-left {
    float: right;
  }

  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        &.sidebar-icon {
          .sidebar-wrapper {
            position: fixed;
            border-bottom: none;
            z-index: 9;
            height: auto;
            line-height: inherit;
            background: $white;
            width: 280px;
            text-align: right;
            transition: 0.5s;

            .toggle-sidebar {
              right: unset;
              left: 30px;
            }

            .sidebar-main .sidebar-links {
              padding: 0;

              >li .badge {
                left: 50px !important;
                right: unset !important;
              }
            }

            &.close_icon {
              margin-right: 0;
              margin-left: unset;
              transform: translate(180px);

              .logo-icon-wrapper {
                text-align: left;

                img {
                  margin-left: 8px;
                }
              }

              ~.page-body,
              ~footer {
                margin-left: 0;
                margin-right: 100px;
                transition: 0.5s;
              }

              .sidebar-main {
                .sidebar-links {
                  >li {
                    a {
                      svg {
                        float: left;
                        margin-left: 0;
                      }
                    }
                  }

                  .sidebar-list {
                    .sidebar-title {
                      &.active {
                        ~.sidebar-submenu {
                          display: none !important;
                        }
                      }
                    }
                  }
                }
              }

              .according-menu {
                display: none;
              }

              &:hover {
                .sidebar-main {
                  .sidebar-links {
                    >li {
                      a {
                        svg {
                          margin-left: 13px;
                        }
                      }
                    }
                  }
                }
              }
            }

            &:hover {
              transform: translate(0px);

              .sidebar-main {
                .sidebar-links {
                  .sidebar-list {
                    .sidebar-title {
                      &.active {
                        ~.sidebar-submenu {
                          display: block !important;
                        }
                      }
                    }
                  }

                  >li {
                    a {
                      svg {
                        float: none;
                      }
                    }
                  }
                }
              }

              .according-menu {
                display: block;
              }
            }

            .sidebar-main {
              .sidebar-links {
                >li {
                  .sidebar-submenu {
                    &:after {
                      right: 30px;
                      left: unset;
                    }

                    >li {
                      a {
                        padding-right: 45px !important;
                        padding-left: unset !important;

                        &:after {
                          right: 26px;
                          left: unset;
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          .page-body {
            margin-left: unset;
            margin-right: 280px;
          }

          .sidebar-wrapper {
            text-align: right;

            .sidebar-main {
              .sidebar-links {
                >li {
                  a {
                    svg {
                      margin-right: unset;
                      margin-left: 15px;
                    }
                  }
                }
              }
            }
          }

          .according-menu {
            right: unset;
            left: 20px;
          }
        }
      }
    }
  }

  .color-box {
    button {
      +button {
        margin-left: unset;
        margin-right: 5px;
      }
    }
  }

  .crop-portion {
    margin-left: unset;
